import React from 'react';
import {storiesOf, appContainer, centered} from 'cs-web-components-storybook';
// DO NOT import components use relative path! Only use Registry or
// from component libs(defined externals)
import {HelpReference} from 'cs-web-components-base';

storiesOf('HelpReference', {})
    .addDecorator(centered)
    .addDecorator(appContainer)
    .addWithInfo('default', () => {
        return (
            <HelpReference />
        );
    });



